.background {
  background: url(https://source.unsplash.com/collection/139251/1600x900)
    no-repeat 50% 25%;
  position: absolute;
  background-size: cover;
  width: 100%;
  height: 100%;
}

$small: 700px;
$medium: 900px;

.logo {
  background: url(~assets/img/logo512.png) no-repeat;
  position: absolute;
  margin-left: 10%;
  top: 40%;
  width: 10vh;
  height: 10vh;
  z-index: 10;
}

.header {
  @media screen and (min-width: $small) {
    position: relative;
    top: 25%;
    margin-left: 2%;
    text-transform: capitalize;
    color: white;
    left: auto;
    h1 {
      font-size: 4rem !important ;
      color: white;
    }
    h4 {
      color: white;
      font-size: 2rem !important;
    }
    img {
      position: relative;
      size: 20px;
      width: 40vh;
      top: 0vh;
      left: 2%;
    }
  }
  @media screen and (max-width: $small) {
    display: none;
  }
}

.form {
  @media screen and (max-width: $small) {
    background-color: white;
    position: fixed !important;
    width: 100vw;
    height: 100vh;
    top: 0%;
    left: 0%;
  }
  @media screen and (min-width: $small) {
    background-color: white;
    top: 20%;
    right: 17%;
    position: relative;
    width: 100%;
    border-radius: 20px;
  }
}

.fieldset {
  top: 5%;
  margin-left: 6%;
  margin-top: 10%;
  margin-right: 0%;
  margin-bottom: 10% !important;
  @media screen and (max-width: $small) {
    padding-right: 15%;
  }
  width: 100%;
  position: relative;
}

.formtitle {
  margin-bottom: 0%;
  margin-top: 10%;
  font-size: 1.4rem !important;
}

.errortext {
  font-size: 0.8rem !important;
  max-width: 100% !important;
  //   margin-right: 20% !important;
  // position: relative;
  padding-top: 0%;
  left: 2%;
  right: 2%;
  padding-right: 10%;
  padding-left: 1%;
}
